body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  display: flex; /* Utiliser la flexbox pour aligner les éléments côte à côte */
}

.div1, .div2 {
  width:50% -fit-content; /* Chacun prend 50% de la largeur de la fenêtre */
  box-sizing: border-box; /* Inclure la bordure et le padding dans la largeur définie */
  border: 1px solid #0e922b; /* Ajouter une bordure pour voir les limites des div */
  padding: 10px; /* Ajouter un peu de marge intérieure pour un meilleur aspect */
}